<template><section><html><head></head><body><h1>CylinderGeometry、 CylinderOutlineGeometry</h1>
<ul>
<li><code v-pre="">vc-geometry-cylinder</code> 组件用于加载圆柱体。</li>
<li><code v-pre="">vc-geometry-outline-cylinder</code> 组件用于加载圆柱体轮廓。</li>
<li>需要作为 <code v-pre="">vc-instance-geometry</code> 的子组件使用，可以挂载到 <code v-pre="">vc-primitive</code>。</li>
</ul>
<h2>示例</h2>
<h3>加载圆柱体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @left_click="LEFT_CLICK">
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes" :model-matrix="modelMatrix">
            <vc-geometry-cylinder ref="cylinder" :length="400000.0" :top-radius="200000.0" :bottom-radius="200000.0" :slices="1024" :vertex-format="vertexFormat"></vc-geometry-cylinder>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes" :model-matrix="modelMatrixOutline">
            <vc-geometry-outline-cylinder ref="cylinderOutline" :length="400000.0" :top-radius="200000.0" :bottom-radius="200000.0" :slices="1024" :number-of-vertical-lines="128"></vc-geometry-outline-cylinder>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
