<h2>属性</h2>
<h3><code v-pre="">vc-geometry-cylinder</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>length</td>
<td>Array</td>
<td></td>
<td><code v-pre="">required</code> 指定圆柱体长度。</td>
</tr>
<tr>
<td>topRadius</td>
<td>Number</td>
<td></td>
<td><code v-pre="">required</code> 指定圆柱体顶部半径。</td>
</tr>
<tr>
<td>bottomRadius</td>
<td>Number</td>
<td></td>
<td><code v-pre="">required</code> 指定圆柱体底部半径。</td>
</tr>
<tr>
<td>slices</td>
<td>Number</td>
<td><code v-pre="">128</code></td>
<td><code v-pre="">optional</code> 指定圆柱圆周边数。</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定顶点属性渲染方式。</td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre="">vc-geometry-outline-cylinder</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>length</td>
<td>Array</td>
<td></td>
<td><code v-pre="">required</code> 指定圆柱体长度。</td>
</tr>
<tr>
<td>topRadius</td>
<td>Number</td>
<td></td>
<td><code v-pre="">required</code> 指定圆柱体顶部半径。</td>
</tr>
<tr>
<td>bottomRadius</td>
<td>Number</td>
<td></td>
<td><code v-pre="">required</code> 指定圆柱体底部半径。</td>
</tr>
<tr>
<td>slices</td>
<td>Number</td>
<td><code v-pre="">128</code></td>
<td><code v-pre="">optional</code> 指定圆柱圆周边数。</td>
</tr>
<tr>
<td>numberOfVerticalLines</td>
<td>Number</td>
<td><code v-pre="">16</code></td>
<td><code v-pre="">optional</code> 指定圆柱体顶部到底部的线条数。</td>
</tr>
</tbody>
</table>
<p>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CylinderGeometry.html">CylinderGeometry</a></strong>、<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CylinderOutlineGeometry.html">CylinderOutlineGeometry</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          geometry: null,
          attributes: null,
          modelMatrix: null,
          vertexFormat: null,
          modelMatrixOutline: null
        }
      },
      mounted() {
        Promise.all([this.$refs.cylinder.createPromise, this.$refs.cylinderOutline.createPromise]).then((instances) => {
          console.log('All geometries are loaded.')
          const boundingSphereUnion = instances.reduce((prev, cur) => {
            const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            const boundingSphere = cur.vm.$parent.modelMatrix
              ? Cesium.BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
              : geometry.boundingSphere
            return prev === null ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
          }, null)
          instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.viewer = viewer
          const { Matrix4, PerInstanceColorAppearance, Transforms, Cartesian3, ColorGeometryInstanceAttribute } = Cesium
          this.appearance = new PerInstanceColorAppearance({
            flat : true
          })
          this.vertexFormat = PerInstanceColorAppearance.VERTEX_FORMAT
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random(), 0.5)
          }
          this.modelMatrix = Matrix4.multiplyByTranslation(
            Transforms.eastNorthUpToFixedFrame(Cartesian3.fromDegrees(105.0, 35.0)),
            new Cartesian3(0, 0, 0),
            new Matrix4()
          )
          this.modelMatrixOutline = Matrix4.multiplyByTranslation(
            Transforms.eastNorthUpToFixedFrame(Cartesian3.fromDegrees(110.0, 35.0)),
            new Cartesian3(0, 0, 0),
            new Matrix4()
          )
        },
        LEFT_CLICK(movement) {
          const feature = this.viewer.scene.pick(movement.position)
          console.log(feature)
        }
      }
    }
  </script>